.myModal {
   position: fixed;
   top: 0;
   bottom: 0;
   right: 0;
   left: 0;
   display: none;
   background: rgb(0, 0, 0, 0.5);
}

.myModalContent {
   padding: 25px;
   background: #fff;
   border-radius: 16px;
   min-width: 250px;
}

.myModal.active {
   display: flex;
   justify-content: center;
   align-items: center;
}
@media (max-width: 700px) {
   .myModalContent {
      padding: 40px 15px;
   }
}
